'use client';
import { useEffect, useState } from 'react';
import apiClient from '../../../lib/api-client';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import TextField from '@mui/material/TextField';
import CircularProgress from '@mui/material/CircularProgress';
import Tooltip from '@mui/material/Tooltip';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';

export default function HubsPage(){
  const [rows,setRows] = useState([]);
  const [loading,setLoading] = useState(false);
  const [modalOpen,setModalOpen] = useState(false);
  const [editing,setEditing] = useState(null);

  async function load(){
    setLoading(true);
    try{
      const data = await apiClient('/master/hubs');
      setRows(Array.isArray(data)?data:[]);
    }catch(err){ console.error(err); alert(err?.body?.message||err?.message||'Load failed'); }
    finally{ setLoading(false); }
  }

  useEffect(()=>{ load(); },[]);

  function openCreate(){ setEditing({ name:'', code:'', description:'' }); setModalOpen(true); }
  function openEdit(r){ setEditing({...r}); setModalOpen(true); }

  async function save(){
    if(!editing || !editing.name) return alert('Name required');
    try{
      if(editing.id){ await apiClient(`/master/hubs/${editing.id}`, { method: 'PATCH', body: editing }); }
      else { await apiClient('/master/hubs', { method: 'POST', body: editing }); }
      setModalOpen(false); setEditing(null); await load();
    }catch(err){ alert(err?.body?.message||err?.message||'Save failed'); }
  }

  async function remove(id){ if(!confirm('Hapus hub?')) return; try{ await apiClient(`/master/hubs/${id}`, { method: 'DELETE' }); await load(); }catch(err){ alert(err?.body?.message||err?.message||'Delete failed'); } }

  return (
    <Box sx={{ p:2 }}>
      <Box sx={{ display:'flex', justifyContent:'space-between', alignItems:'center', mb:2 }}>
        <Box>
          <Box component="h2" sx={{ m:0 }}>Hubs</Box>
          <Box sx={{ color:'text.secondary', fontSize:13 }}>Manage planner hubs</Box>
        </Box>
        <Box>
          <Button variant="outlined" onClick={load} startIcon={loading? <CircularProgress size={18}/> : null}>Refresh</Button>
          <Button variant="contained" sx={{ ml:1 }} startIcon={<AddIcon/>} onClick={openCreate}>Create Hub</Button>
        </Box>
      </Box>

      <Paper>
        <TableContainer>
          <Table size="small">
            <TableHead>
              <TableRow><TableCell>ID</TableCell><TableCell>Code</TableCell><TableCell>Name</TableCell><TableCell>Description</TableCell><TableCell align="right">Action</TableCell></TableRow>
            </TableHead>
            <TableBody>
              {rows.map(r=> (
                <TableRow key={r.id} hover>
                  <TableCell>{r.id}</TableCell>
                  <TableCell>{r.code||'-'}</TableCell>
                  <TableCell>{r.name}</TableCell>
                  <TableCell sx={{ maxWidth:300, overflow:'hidden', textOverflow:'ellipsis', whiteSpace:'nowrap' }}>{r.description}</TableCell>
                  <TableCell align="right">
                    <Tooltip title="Edit"><IconButton size="small" onClick={()=>openEdit(r)}><EditIcon fontSize="small"/></IconButton></Tooltip>
                    <Tooltip title="Delete"><IconButton size="small" color="error" onClick={()=>remove(r.id)}><DeleteIcon fontSize="small"/></IconButton></Tooltip>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>

      <Dialog open={modalOpen} onClose={()=>{ setModalOpen(false); setEditing(null); }} fullWidth>
        <DialogTitle>{editing?.id? 'Edit Hub' : 'Create Hub'}</DialogTitle>
        <DialogContent>
          <Box sx={{ display:'grid', gap:2, mt:1 }}>
            <TextField label="Code" size="small" value={editing?.code||''} onChange={e=>setEditing({...editing, code: e.target.value})} />
            <TextField label="Name" size="small" value={editing?.name||''} onChange={e=>setEditing({...editing, name: e.target.value})} />
            <TextField label="Description" size="small" multiline minRows={3} value={editing?.description||''} onChange={e=>setEditing({...editing, description: e.target.value})} />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={()=>{ setModalOpen(false); setEditing(null); }}>Cancel</Button>
          <Button variant="contained" onClick={save}>Save</Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}
